
:root{
  --primary-white: #fff;
  --primary-purple: #ff4d5a;
  --primary-dark: rgba(255,255,255,0);
}
a{
  text-decoration:none; 
  color:inherit;
}
html {
  scroll-behavior: smooth !important;
}

body {
  font-family: 'aldrich';
}


nav a.nav-link {
  font-size: .9rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;
}

.navbar-brand{
  color:var(--primary-white) !important;
  font-family: 'Aldrich';
  padding: 0 !important;

}

.navbar-nav > li > a{
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color:var(--primary-white) !important;
  font-family:'Aldrich';
  border-top: 0.1875rem solid var(--primary-dark);
}

nav ul li{
  text-transform: uppercase;
}

.navbar-toggler-icon{
  color:var(--primary-white) !important;
}

.navbar-nav > li > a.active, .navbar-nav > li > a.active:focus, .navbar-nav > li > a:hover {
  border-top: 0.1875rem solid var(--primary-purple);
}


/** header */

.header-wrapper {
  position: relative;
  background: url(background.svg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 91vh;
}

@media screen and (max-width:800px) {
  .header-wrapper {
   height: 92.8vh;
  }
 }
 @media screen and (max-width:420px) {
  .header-wrapper{
   height: 93vh;
  }
 }

/* .main-info{
  display:flex;
  flex-direction: column;
  justify-content: start;
  align-items: left;
  text-align: left;
  font-family: 'aldrich';
} */

.main-info h1 {
  font-size:9rem;
  line-height:1;
  letter-spacing:0.1em;
  margin-bottom:1.8rem;
  font-family:"aldrich";
  font-weight:normal;
  text-shadow:0 10px 30px rgba(2, 11, 22, 0.5)
 }
 @media screen and (max-width:800px) {
  .main-info h1 {
   font-size:4.5rem
  }
 }
 @media screen and (max-width:420px) {
  .main-info h1 {
   font-size:3.5rem;
   margin-bottom:0.8rem;
   text-shadow:0 3px 10px rgba(2, 11, 22, 0.5)
  }
 }

.main-info {
  color:#fff;
  position:absolute;
  overflow:hidden;
  top:50%;
  -webkit-transform:translate3d(0, -50%, 0);
  transform:translate3d(0, -50%, 0);
  z-index:200;
  margin-left:11%;
  font-family: 'aldrich';
 }
 @media screen and (max-width:800px) {
  .main-info {
   margin-left:40px
  }
 }
 @media screen and (max-width:420px) {
  .main-info {
   width:87.5%;
   margin:0 6.25%;
   top:auto;
   -webkit-transform:translate3d(0, 0, 0);
   transform:translate3d(0, 0, 0);
   bottom:8rem
  }
 }
 @media screen and (max-width:420px) {
  .main-info {
   margin:0 13%
  }
 }
 .main-info p {
  color:#fff;
  font-size:2.8rem;
  line-height:1.5;
  letter-spacing:0.1em
 }
 @media screen and (max-width:800px) {
  .main-info p {
   font-size:2.4rem;
   line-height:1.4
  }
 }
 @media screen and (max-width:420px) {
  .main-info p {
   font-size:1.2rem
  }
 }


 .wrapper {
  margin-bottom:20rem;
  position:relative;
  z-index:200
 }
 @media screen and (max-width:420px) {
  .wrapper {
   margin-bottom:12rem
  }
 }

 .container-about{
   margin-top: 20rem;
 }

 .text--top {
  -webkit-box-align:start;
  -ms-flex-align:start;
  align-items:flex-start;
  display: flex;
 }

 .text__wrap--top {
  -webkit-box-align:start;
  -ms-flex-align:start;
  align-items:flex-start;
  display: flex;
 }
 @media screen and (max-width:800px) {
  .text__wrap--top {
   -webkit-box-align:center;
   -ms-flex-align:center;
   align-items:center
  }
 }
 @media screen and (max-width:800px) {
  .text__wrap {
   -webkit-box-orient:vertical;
   -webkit-box-direction:normal;
   -ms-flex-direction:column;
   flex-direction:column;
   padding-left:10%
  }
 }

 .heading {
  font-size:4rem;
  color:#072142;
  letter-spacing:0.2em;
  font-family:"aldrich";
  margin-right:3.2rem;
  margin-left: 3rem;
  padding-top:1.2rem
 }

 .heading--portofolio{
  font-size:3rem;
 } 
 .heading--top {
  padding-top:0
 }
 @media screen and (max-width:420px) {
  .heading--left {
   margin-left:auto
  }
 }
 @media screen and (max-width:800px) {
  .heading {
   padding-top:0;
   margin-right:0
  }
 }
 @media screen and (max-width:420px) {
  .heading {
   font-size:2.4rem;
   padding-top:0.2rem
  }
 }

 .who__wrap {
  width:48.333%
 }
 @media screen and (max-width:800px) {
  .who__wrap {
   width:100%;
   margin-top:7.2rem
  }
 }
 @media screen and (max-width:420px) {
  .who__wrap {
   margin-top:4rem
  }
 }
 .who__name {
  color:#072142;
  margin-bottom:2.4rem
 }
 @media screen and (max-width:420px) {
  .who__name {
   margin-bottom:1.6rem
  }
 }
 .who__en {
  font-size:1.8rem;
  letter-spacing:0.2em;
  font-family:"aldrich";
  line-height:1.3
 }
 @media screen and (max-width:420px) {
  .who__en {
   font-size:1rem
  }
 }
 .who__text {
  margin-bottom:6.4rem;
  font-family: 'aldrich';
 }
 @media screen and (max-width:420px) {
  .who__text {
   margin-bottom:3.2rem
  }
 }
 .who__image {
   width: 10px;
  -webkit-box-shadow:0px 63px 154px -20px rgba(7, 33, 66, 0.5);
  box-shadow:0px 63px 154px -20px rgba(7, 33, 66, 0.5)
 }
 @media screen and (max-width:420px) {
  .who__image {
   -webkit-box-shadow:0px 33px 84px -20px rgba(7, 33, 66, 0.5);
   box-shadow:0px 33px 84px -20px rgba(7, 33, 66, 0.5)
  }
 }

 img.about-img{
  height:auto;
  vertical-align:middle;
  width:78.5vh
 }

 @media screen and (max-width:800px) {
  img.about-img{
   width:20rem
  }
 }
 @media screen and (max-width:420px) {
  img.about-img {
   width:10rem
  }
 }

.section-porto-wrap {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  justify-content: space-around;
  align-items: stretch;
  align-content: stretch;
  margin-top: 80px;
}

.porto-wrap{
  display: flex;
  flex-direction: row;
  justify-content: start;
  margin-left: 100px;
}

.description-porto{
  margin-top: 50px;
}

.porto__text{
  margin-bottom:2.4rem;
  font-family: 'aldrich';
}

.description-porto .btn {
  padding:12px 30px;
  border-radius:30px;
  display:inline-block;
  font-weight:400;
 }


 /*footer*/
 .container-fluid {
     overflow: hidden;
     margin-top: 250px;
     background: #262626;
     color: #627482 !important;
     margin-bottom: 0;
     padding-bottom: 0
 }

 .bold-text {
     color: #989c9e !important
 }

 .mt-55 {
     margin-top: calc(50px + (60 - 50) * ((100vw - 360px) / (1600 - 360))) !important
 }

 h3.heading-contact {
     font-size: calc(34px + (40 - 34) * ((100vw - 360px) / (1600 - 360))) !important;
     font-family: 'aldrich';
 }

 .icon-wrap{
   display: flex;
   flex-direction: row;
   justify-content: space-around;
   font-size: 2.8rem;
   margin-top: 50px;
   margin-bottom: 10px;
 }
